*{
	margin:0;
	padding:0;

  -webkit-box-sizing: border-box;

          box-sizing: border-box;
}
html,body{
	height:100%;
  width: 100%;
}

#root{
  height:100%;
}

.flex-center{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.flex-right{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.wrapper{
  height: 100%;
  width:100%;

  margin:auto;

	font-family: sans-serif;
	font-size: 1rem;
}

.header{
  width: 100%;
  height: 5%;

  padding-right: 5%;

  background: #333;
  color: #ccc;
}

.content{
  width:100%;
  height:95%;

  background: -webkit-gradient(linear, left top, left bottom, from(#fafafa), to(#eee));

  background: -webkit-linear-gradient(#fafafa, #eee);

  background: -o-linear-gradient(#fafafa, #eee);

  background: linear-gradient(#fafafa, #eee);
	/* background: #fff; */
}

.todo-container{

	-ms-flex-direction: column;

	    flex-direction: column;

	width: 50%;
	max-width: 500px;
	height: 75%;

	background: #eee;
	border: 1px solid #dadada;
	border-radius: 5px;

	-webkit-box-shadow: 1px 1px #dadada,
							2px 2px #dadada,
							3px 3px #dadada,
							4px 4px #dadada,
							6px 6px #dadada,
							7px 7px #dadada,
							8px 8px #dadada,
							9px 9px #dadada,
							10px 10px #dadada;

	        box-shadow: 1px 1px #dadada,
							2px 2px #dadada,
							3px 3px #dadada,
							4px 4px #dadada,
							6px 6px #dadada,
							7px 7px #dadada,
							8px 8px #dadada,
							9px 9px #dadada,
							10px 10px #dadada;
}

.todo-input-div{
	width:100%;
	padding: 15px 15px 0 15px;
}

.todo-input{
	width: 100%;
	padding: 5px;

	border-radius: 5px;
	border: 1px solid #ccc;
	background: #f8f8f8;
}

.todo-list{
	width:100%;
	height:100%;

	border: 15px solid rgba(0,0,0,0);
	border-radius: 20px;
	background: #dadada;
	background-clip: padding-box;

}

.todo-list li{
	padding: 5px;
	list-style: none;

	color: #333;
}

.todo-list li:nth-child(even){
	background: #eee;
}



